      backword: false,
      forword: true,
      swiperOptions: {
          // Some Swiper option/callback...
          slidesPerView: 3.5,
          spaceBetween: 30,
          simulateTouch: true,
        }
    }
  },
  methods: {
    toggleDropDown(e){
      if(!e.target.classList.contains('active')){
        e.target.querySelector('.dropdown-menu').style.cssText = 'pointer-events: auto; opacity: 1'
        e.target.classList.add('active')
      } else {
        e.target.querySelector('.dropdown-menu').style.cssText = 'pointer-events: none; opacity: 0'
        e.target.classList.remove('active')
      }
      
    },
  }
}

</script>

