<template>
  <section id="music-box">
    <img :src='image' :class="{'rounded': round}" alt='music'/>
    <h4 class="name"><a href="#">{{randName}}</a></h4>
    <div class="info" v-if="!round">
      <p><a href="#" class="author">The Weekend</a></p>
      <p v-if="collection" class="followers">22,351 <span class="upper">Followers</span></p>
    </div>
  </section>
</template>

<script>
export default {
  name: 'music-box',
  props: ['image', 'round'],
  data: function(){
    return {
      singer: false,
      music: true,
      collection: true,
    }
  },
  computed: {
    randName(){
      let rand = Math.floor(Math.random() * 5)
      let names = ['quam culpa officia','earum et ullam','fuga numquam eos','modi sint eveniet','quidem molestias molestiae']
      return names[rand]
    }
  }
}
</script>

<style lang="scss">
#music-box{
  margin-right: 1rem;
  .rounded{
    border-radius: 99rem;
    width: 100px;
    height: 100px !important;
  }
  img{
    width: 100%;
    height: 120px !important;
    object-fit: cover;
    background-color: #111;
    display: block;
  }
  h4{
    margin-bottom: 0.5rem;
    a{
      color: white;
      cursor: pointer;
      &:hover{text-decoration: underline;}
    }
  } 
  .info{
    p{
      margin-top: 0;
      font-size: 0.8rem;
      display: flex;
      gap: 0.5rem;
      a:hover{text-decoration: underline;}
    }
  }
}
</style>