<template>
  <nav id="main-nav" class="container bg-dark-100">
    <a href="#"><i class="fas fa-ellipsis-h "></i></a>
    <!-- nav list -->
    <ul class="nav-list">
      <li><a href="#" class="active" @click.prevent="activateLink">
        <i class="fas fa-home"></i>
        <span class="bold">Home</span>
      </a></li>
      <li><a href="#" @click.prevent="activateLink">
        <i class="fas fa-compact-disc "></i>
        <span class="bold">Browse</span>
      </a></li>
      <li><a href="#" @click.prevent="activateLink">
        <i class="fas fa-broadcast-tower "></i>
        <span class="bold">Radio</span>
      </a></li>
    </ul>
    <div class="specials">
      <!-- your library -->
      <section class="your-library">
        <h4 class="upper normal">Your Library</h4>
        <ul>
          <li><a href="#" class="bold">Made For You</a></li>
          <li><a href="#" class="bold">Recently Played</a></li>
          <li><a href="#" class="bold">Liked Songs</a></li>
          <li><a href="#" class="bold">Albums</a></li>
          <li><a href="#" class="bold">Artists</a></li>
        </ul>
      </section>
      <!-- playlists -->
      <section class="playlists">
        <h4 class="upper normal">Playlists</h4>
        <ul>
          <li><a href="#">Global Top 50</a></li>
          <li><a href="#">Naruto Shippuden OST</a></li>
          <li><a href="#">PES 2010 soundtrack</a></li>
        </ul>
      </section>
    </div>
    <!-- add playlist -->
    <section class="add-playlist">
      <div class="divider"></div>
      <p>
        <a href="#">
          <i class="fas fa-plus"></i>
          <span>New Playlist</span>
        </a>
      </p>
    </section>
  </nav>
</template>

<script>
export default {
  name: '',
  methods: {
    activateLink(event){
      // remove all actives first
      document.querySelectorAll('.nav-list li a').forEach(link => {
        link.classList.remove('active')
      })
      // add active to chosen link
      event.target.classList.add('active')
    }
  }
}
</script>

<style lang="scss" scoped>
$main-c: #1ED760;
#main-nav{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  .nav-list{
    margin-top: 1rem;
    margin-bottom: 1rem;
    width: 100%;
    li{
      width: 100%;
      margin-bottom: 0.4rem;
      a{
        padding: 0.4rem 0;
        width: 100%;
        position: relative;
        i{
          pointer-events: none;
          font-size: 1.4rem;
          width: 2.5rem;
          text-align: left;
        }
        &:after{
          content: '';
          position: absolute;
          left: -1rem;
          height: 70%;
          width: 3px;
          background-color: transparent;
        }
        &.active{
          color: #ffffff;
          &:after{
            background-color: $main-c;
          }
        }
        span{pointer-events: none;}
      }
    }
  }
  .specials{
    font-size: 14px;
    width: 100%;
    overflow-y: scroll;
    &::-webkit-scrollbar {
      width: 0.5rem;
    }
    &::-webkit-scrollbar-track{
      background-color: transparent;
    }
    &::-webkit-scrollbar-thumb{
      background-color: #333;
    }
    .your-library, .playlists{
      margin-bottom: 2rem;
      ul{
        width: 100%;
        li{
          width: 100%;
          margin-bottom: 0.4rem;
          a{
            padding: 0.2rem 0;
            width: 100%;
          }
        }
      }
    }
  }
  .add-playlist{
    margin-top: 1rem;
    border-top: 1px solid #333;
    margin-top: 1rem;
    width: 100%;
    p{
      a{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 0.5rem;
        i{
          font-size: 1.2rem;
          width: 1.7rem;
          height: 1.7rem;
          border-radius: 99rem;
          border: 1px solid;
          text-align: center;
          line-height: 1.7rem;
        }
      }
    }
  }
}
</style>