<template>
  <section id="player" class="container bg-dark-300">
    <div class="music">
      <a href="#" class="pointer"><img src='http://fakeimg.pl/50/#333?font=lobster' alt='music_poster'/></a>
      <div>
        <h4 class="music_name"><a href="#" class="normal white">Fire On Fire - From "Wathership"</a></h4>
        <p class="author"><a href="#">Sam Smith</a></p>
      </div>
      <i class="far fa-heart" :class="{fas: liked}" @click="liked = !liked"></i>
    </div>
    <div class="controls">
      <div class="ctrl">
        <a href="#"><i class="fas fa-random"></i></a>
        <a href="#"><i class="fas fa-step-backward"></i></a>
        <a href="#" id="play-btn"><i class="fas" :class="{'fa-pause': paused, 'fa-play': !paused}" @click.prevent="playToggler($event)"></i></a>
        <a href="#"><i class="fas fa-step-forward"></i></a>
        <a href="#"><i class="fas fa-sync" @click.prevent="spin" style="transition: all 0.5s"></i></a>
      </div>
      <div class="progress">
        <span>0:00</span> <input type="range" min="1" :max="time_length" :value="progressVal" @click="progressEffect" class="slider"> <span>{{min + ':' + sec}}</span>
      </div>
    </div>
    <div class="else">
      <a href="#"><i class="fas fa-list-ul"></i></a>
      <a href="#"><i class="fas fa-tablet-alt"></i></a>
      <a href="#"><i class="fas" :class="{'fa-volume-mute': muted, 'fa-volume-up': !muted}" @click="muted = !muted"></i></a>
      <input type="range" min="1" max="100" class="volume">
    </div>
  </section>
</template>

<script>
export default {
  name: '',
  data: function(){
    return {
      liked: false,
      paused: false,
      progressVal: 0,
      time_length: 36,
      sec: '00',
      min: '0',
      muted: true,
    }
  },
  computed: {
    
  },
  created: function(){
    let min = Math.floor(this.time_length / 60),
        sec = this.time_length % 60;
    this.min = min
    this.sec = sec < 10 ? '0' + sec : sec
  },
  methods: {
    playToggler(e){
      this.paused = !this.paused
      e.target.parentElement.nextElementSibling.style.cssText = 'color: rgba(255,255,255,0.2); pointer-events: none'
      e.target.parentElement.previousElementSibling.style.cssText = 'color: rgba(255,255,255,0.2); pointer-events: none'
    },
    progressEffect(e){
      this.progressVal = e.target.value
    },
    spin(e){
      if(e.target.style.transform == 'rotate(180deg)')
        e.target.style.transform = 'rotate(0deg)'
      else
        e.target.style.transform = 'rotate(180deg)'
    }
  }
}
</script>

<style lang="scss" scoped>
#player{
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  justify-content: space-between;
  align-items: center;
  padding: .5rem 1rem;
  gap: 2rem;
  .music{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    div{
      h4, p{
        margin: 0.2rem 0;
        a{
          cursor: pointer;
          &:hover{text-decoration: underline;}
        }
      }
    }
  }
  .controls{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .ctrl{
      a{
        margin: 1rem;
      }
      #play-btn{
        font-size: 1.3rem;
        &:hover{
          transform: scale(1.05);
        }
      }
    }
    .progress{
      display: flex;
      justify-content: center;
      align-items: center;
      span{
        display: inline-block;
        margin: 0 0.5rem;
      }
      .slider {
        -webkit-appearance: none;
        appearance: none;
        width: 35vw;
        height: 4px;
        background: rgba(255,255,255,0.1);
        opacity: 0.7;
        padding: 0;
      }
      .slider:hover {
        opacity: 1;
      }
      .slider::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        width: 8px;
        height: 8px;
        border-radius: 5px;
        background: #fff;
        cursor: pointer;
      }
      .slider::-moz-range-thumb {
        width: 40px;
        height: 4px;
        background: #4CAF50;
        cursor: pointer;
      }
    }
  }
  .else{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    a{
      margin-left: 1rem;
    }
    .volume {
      -webkit-appearance: none;
      appearance: none;
      width: 5rem;
      margin-left: 1rem;
      height: 4px;
      background: rgba(255,255,255,0.1);
      opacity: 0.7;
      padding: 0;
    }
    .volume:hover {
      opacity: 1;
    }
    .volume::-webkit-slider-thumb {
      -webkit-appearance: none;
      appearance: none;
      width: 8px;
      height: 8px;
      border-radius: 5px;
      background: #fff;
      cursor: pointer;
    }
    .volume::-moz-range-thumb {
      width: 40px;
      height: 4px;
      background: #4CAF50;
      cursor: pointer;
    }
  }
}
</style>